.simple-color-picker{display:flex;flex-wrap:wrap}.simple-color-picker .color-wrapper{border-radius:50%;width:18px;height:18px;cursor:pointer;margin-right:6px;margin-bottom:6px}.simple-color-picker .color-wrapper:hover{transform:scale(1.1);transition:transform 0.2s ease}.simple-color-picker .color-wrapper .selected-color{color:#fff}
