.input-checkbox-wrapper{position:relative;display:flex;align-items:center;font-size:14px}.input-checkbox-wrapper .input-checkbox-inner{position:relative;top:0;left:0;display:inline-block;width:14px;height:14px;border-radius:2px;border:1px solid #bbb;background:#fff;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-transition:all 0.3s;transition:all 0.3s}.input-checkbox-wrapper .input-checkbox-inner:after{box-sizing:content-box;position:absolute;display:block;content:" ";font-size:0;top:3px;left:2px;width:6px;height:3px;background:transparent;border:2px solid #fff;border-top:none;border-right:none;transform:rotate(-45deg) scale(0);transition:all 0.12s ease-in-out}.input-checkbox-wrapper .input-checkbox-inner--label{margin-left:5px}.input-checkbox-wrapper.is-checked .input-checkbox-inner{background-color:#38f;border-color:#27f}.input-checkbox-wrapper.is-checked .input-checkbox-inner:after{transform:rotate(-45deg) scale(1)}.input-checkbox-wrapper input{position:absolute;left:0;right:0;bottom:0;top:0;margin:0;padding:0;width:100%;height:100%;z-index:1;opacity:0;cursor:pointer}
