.bxu-input-wrapper{position:relative;min-width:100px;height:30px;display:flex;border:0}.bxu-input-wrapper>*{box-sizing:border-box}.bxu-input-wrapper>.bxu-input-addon-before:first-child{border-top-left-radius:4px;border-bottom-left-radius:4px}.bxu-input-wrapper>.bxu-input-addon-after:last-child{border-top-right-radius:4px;border-bottom-right-radius:4px}.bxu-input-wrapper .bxu-input{padding:4px 8px;flex-grow:1;height:100%;max-width:100%;line-height:14px;font-size:12px;outline:none;border-radius:2px;border:1px solid #bbb}.bxu-input-wrapper .bxu-input:focus{border:1px solid #3498db}.bxu-input-wrapper .bxu-input-addon-before,.bxu-input-wrapper .bxu-input-addon-after{background:#cdcdcd;color:#1a1a1a;height:100%;padding:0 5px;font-size:12px;line-height:28px;white-space:nowrap}.bxu-input-wrapper .clear-icon{position:absolute;height:100%;line-height:30px;right:4px;font-size:13px;color:#bbb;cursor:pointer}.bxu-textarea-wrapper{height:auto}.bxu-textarea-wrapper textarea{outline:none;resize:vertical;border-radius:2px;padding:5px 10px;line-height:1.5;width:100%;min-height:100px;border:1px solid #b3b3b3}.bxu-textarea-wrapper .bxu-textarea_showCount{position:absolute;bottom:0;right:15px;font-size:12px;color:#b3b3b3}
