.btn{border-radius:4px;border:1px solid #dadce0;font-size:14px;letter-spacing:0.25px;outline:none;cursor:pointer}.btn.is-inform{line-height:36px;padding:0 24px;transition:background 0.2s ease-in;background:#1a73e8;color:#fff}.btn.is-inform:hover{background:#488fed}.btn.is-silent{border:none}.btn.is-silent:hover{background:#e8e9ec}.img{max-width:100%}ul{list-style-type:none}a{text-decoration:none}.is-ellipse{border-radius:30px}.is-separator{display:block;width:100%;height:1px;background:#dadce0}.font-subtitle{font-size:12px;font-weight:400;color:#70757a}.font-calEvent{font-size:12px;font-weight:300;color:#fff}.font-layout-option{font-size:14px;font-weight:400}.inline-block{display:inline-block}.is-grey{color:#70757a}.is-blue{color:#1a73e8}.is-lighter-gey{color:#80868b}.no-select{user-select:none}.icon-circle-wrapper{position:relative;width:30px;height:30px;display:flex;align-items:center;justify-content:center;border-radius:50%;color:#3c4043;cursor:pointer;transition:background 0.2s ease-in}.icon-circle-wrapper:hover{background:#e8e9ec}.action-text-wrapper{position:relative;display:flex;align-items:center;justify-content:center;border-radius:6px;padding:5px;cursor:pointer;transition:background 0.2s ease-in}.action-text-wrapper:hover{background:#edeef0}@keyframes placeHolderShimmer{0%{background-position:-468px 0}100%{background-position:468px 0}}.cal-animated-background{animation-duration:1s;animation-fill-mode:forwards;animation-iteration-count:infinite;animation-name:placeHolderShimmer;animation-timing-function:linear;background:#f6f7f8;background:linear-gradient(to right, #eee 8%, #ddd 18%, #eee 33%);background-size:800px 104px;position:relative}.itemWrapper.is-normal{position:relative;height:22px;width:100%;padding-bottom:2px;overflow-y:hidden}.itemWrapper.is-normal .is-timingEvt{color:#3c4043}.itemWrapper.is-normal .is-timingEvt:hover{background:#e8e9ec}.itemWrapper.is-normal .calday-simpleevt-item{align-items:center}.itemWrapper.is-normal .cal-text{flex:1 1 0%}.itemWrapper.is-timing{position:absolute;width:100%}.itemWrapper.is-timing .is-timingEvt{color:#fff}.itemWrapper.is-timing .is-timingEvt:hover{background:#e8e9ec}.itemWrapper.is-timing .cal-text{margin-right:10px}.itemWrapper.is-timing .calday-simpleevt-item{border:1px solid #fff;flex-wrap:wrap}.itemWrapper.is-timing .cal-date{line-height:20px}.itemWrapper .simpleItem-selected{box-shadow:0 1px 3px 0 rgba(60,64,67,0.302),0 4px 8px 3px rgba(60,64,67,0.149)}.calday-simpleevt-item{position:relative;display:flex;text-align:left;width:100%;padding-left:10px;border-radius:4px;height:100%;font-size:11px;cursor:pointer;transition:all 0.2s ease;overflow-x:hidden;overflow-y:hidden}.calday-simpleevt-item.is-allday{color:#fff}.calday-simpleevt-item.is-allday:hover{opacity:0.8}.calday-simpleevt-item.is-allday.is-reminder{background:#3f51b5}.calday-simpleevt-item .cal-unit{margin-right:5px;display:flex;align-items:center}.calday-simpleevt-item .cal-dot{flex:0 0 15px}.calday-simpleevt-item .cal-dot b{display:block;width:8px;height:8px;border-radius:50%}.calday-simpleevt-item .cal-icon{flex:0 0 15px}.calday-simpleevt-item .cal-text{font-weight:600;line-height:20px;text-overflow:clip;white-space:nowrap;overflow:hidden}
