.simple-calgrid-container{position:relative;padding:2px}.simple-calgrid-container .value-wraper{position:relative;display:flex;justify-content:center;align-items:center;width:100%;height:100%;font-size:13px;border-radius:50%;transition:background .3s linear}.simple-calgrid-container .value-wraper:hover{background:#EEEDED;cursor:pointer}.simple-calgrid-container .value-wraper.is-today{background:#1a73e8;color:#fff}.simple-calgrid-container .value-wraper.is-selected:not(.is-today){background:#a5c8f6}.simple-calgrid-container .value-wraper.is-grey{color:#b1b3b4}.simple-calgrid-container .value-wraper.is-grey.is-selected,.simple-calgrid-container .value-wraper.is-grey.is-today{color:#fff}
