.btn{border-radius:4px;border:1px solid #dadce0;font-size:14px;letter-spacing:0.25px;outline:none;cursor:pointer}.btn.is-inform{line-height:36px;padding:0 24px;transition:background 0.2s ease-in;background:#1a73e8;color:#fff}.btn.is-inform:hover{background:#488fed}.btn.is-silent{border:none}.btn.is-silent:hover{background:#e8e9ec}.img{max-width:100%}ul{list-style-type:none}a{text-decoration:none}.is-ellipse{border-radius:30px}.is-separator{display:block;width:100%;height:1px;background:#dadce0}.font-subtitle{font-size:12px;font-weight:400;color:#70757a}.font-calEvent{font-size:12px;font-weight:300;color:#fff}.font-layout-option{font-size:14px;font-weight:400}.inline-block{display:inline-block}.is-grey{color:#70757a}.is-blue{color:#1a73e8}.is-lighter-gey{color:#80868b}.no-select{user-select:none}.icon-circle-wrapper{position:relative;width:30px;height:30px;display:flex;align-items:center;justify-content:center;border-radius:50%;color:#3c4043;cursor:pointer;transition:background 0.2s ease-in}.icon-circle-wrapper:hover{background:#e8e9ec}.action-text-wrapper{position:relative;display:flex;align-items:center;justify-content:center;border-radius:6px;padding:5px;cursor:pointer;transition:background 0.2s ease-in}.action-text-wrapper:hover{background:#edeef0}@keyframes placeHolderShimmer{0%{background-position:-468px 0}100%{background-position:468px 0}}.cal-animated-background{animation-duration:1s;animation-fill-mode:forwards;animation-iteration-count:infinite;animation-name:placeHolderShimmer;animation-timing-function:linear;background:#f6f7f8;background:linear-gradient(to right, #eee 8%, #ddd 18%, #eee 33%);background-size:800px 104px;position:relative}.weekLine-container{display:flex;flex-direction:column;width:100%;height:calc(100% - 15px);margin-top:15px}.weekLine-container__placeholder{background:#f1f3f4;flex-basis:calc(30px - 15px);padding-top:10px;border-top-left-radius:5px;border-top-right-radius:5px}.weekLine-container__items{background:#f1f3f4;flex-grow:1}.weekLine-container-item{color:#3c4043;font-size:12px;font-weight:500;border-bottom:1px solid #dadce0;padding-top:5px}
