.bxu-datepicker-header{position:relative;display:flex;justify-content:space-between;align-items:center;padding:0 6px;width:100%;max-width:300px}.bxu-datepicker-header .bxu-header-title{font-size:14px}.bxu-datepicker-header .bxu-header-action{position:relative;width:50px;display:flex;justify-content:space-between}.bxu-datepicker-header .bxu-header-action .action-span{display:flex;justify-content:center;align-items:center;font-size:22px;width:24px;height:24px;font-weight:500;color:#5f6368;border-radius:50%;cursor:pointer}.bxu-datepicker-header .bxu-header-action .action-span:hover{background:#EEEDED}.bxu-datepicker-header .bxu-header-action .action-span .ali-icon{width:0.7em}
